import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import home from '@/pages/home/index'
import article from '@/pages/article/index'

Vue.use(Router)
Vue.use(VueResource)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/article',
      name: 'article',
      component: article,
      children: [
        {
          path: '1',
          name: '1'
        },
        {
          path: 'archive',
          name: 'archive'
        }
      ]
    }
  ]
})



