<template>
  <div class="container typo">
    <h1 class="logo">文章归档</h1>
    <article-title v-for="(item, index) in article" :key="index" :index="index + 1" :href="item.href" :title="item.title" :time="item.time" :memo="item.memo"></article-title>
  </div>
</template>
<script>
import articleTitle from '@/components/common/title'
export default {
  components: {
    articleTitle
  },
  data () {
    return {
      article: [
        {
          href: '#/article/1',
          title: '百度统计使用文档',
          time: '2018/5/27',
          memo: '相关资料和实现代码的简单整理'
        }
      ]
    }
  }
}
</script>




